import "./consiglio.js";
import { Paginazione } from "../../commons/js/render_paginazione.js";
import { StringRenderer } from "../../commons/js/string_renderer.js";
import { Pagina } from "../../commons/js/pagina.js";

$(document).ready(function () {

    var DtoFiltro = function () {
        this.legislatura = null;
        this.anno = null;
    };

    var Legislatura = function () {
        this.id = null;
        this.titolo = null;
        this.anni = [];
    }

    var promises = [];

    function visualizzaElencoFile(elenco) {
        let html = '<ul class="list-unstyled">';
        elenco.components.forEach(item => {
            let attachment = item.components.attachment;
            let link = item.components.link;
            if (item.properties.testo != undefined) {
                html += item.properties.testo;
            }

            if (attachment != undefined) {
                attachment.components.forEach(singleAtt => {
                    let url = apibase.baseurl + singleAtt.properties.address;
                    html += '<a href="' + url + '" target="_blank">' + singleAtt.title + '</a>'
                });
            }
            if (link != undefined) {
                link.components.forEach(singleLink => {
                    let url = singleLink.properties.url;
                    html += '<a href="' + url + '">' + singleLink.title + '</a>'
                });
            }



        });
        html += '</ul>';
        return html;
    }

    function singlePromise(relatedData) {
        $.ajax({
            url: apibase.baseurl + relatedData.refType + "/" + relatedData.singleId,
            success: function (result) {
                if (result) {
                    var html = "<strong>" + result.title + "</strong>";                    
                    $("#related_" + relatedData.singleId).html(html)
                }
            },
        });
    }


    function visualizzaRelatedObj(relatedData) {
        var html = "<p id='related_" + relatedData.singleId + "'>" + "</p>"
        promises.push(relatedData);
        return html;
    }


    const modelloTabella = [
        { label: "Denominazione del Gruppo", tipoCampo: "related", campo: "gruppo", visualizzaCon: visualizzaRelatedObj },
        { label: "Rendiconto", tipoCampo: "component", campo: "rendiconto", visualizzaCon: visualizzaElencoFile },
        { label: "Delibera CdC", tipoCampo: "component", campo: "delibera", visualizzaCon: visualizzaElencoFile },
        { label: "DudP", tipoCampo: "component", campo: "dudp", visualizzaCon: visualizzaElencoFile },
        { label: "Ulteriore documentazione", tipoCampo: "component", campo: "ulteriore_documentazione", visualizzaCon: visualizzaElencoFile },
        { label: "Note", tipoCampo: "property", campo: "note" }
    ];

    function renderColonna(datiColonna) {
        let riga = '';
        for (let colonna of colonne) {
            let valore = null;
            if (colonna.tipoCampo === "property") {
                valore = datiColonna.properties[colonna.campo];
            } else if (colonna.tipoCampo === "component") {
                valore = datiColonna.components[colonna.campo];
            } else if (colonna.tipoCampo === "related") {
                valore = { refType: "10020", singleId: datiColonna.properties[colonna.campo] };
            } else {
                valore = datiColonna[colonna.campo];
            }
            if (valore) {
                if (colonna.visualizzaCon) {
                    valore = colonna.visualizzaCon(valore);
                }
            }
            riga += "<td>" + (valore || '') + "</td>";
        }
        return riga;
    }

    function renderCompleto(result) {
        let intestazioneTabella = '<tr>';
        colonne = modelloTabella;
        for (var colonna of colonne) {
            intestazioneTabella += "<th>" + colonna.label + "</th>";
        }
        intestazioneTabella += "</tr>"
        $("#thead").html(intestazioneTabella);
        const tabella = $("#tbody");
        if (result && result.content) {
            result.content.forEach(function (single) {
                const oid = single.id;
                tabella.append('<tr data-oid="' + oid + '"></tr>');
                const datiColonna = renderColonna(single);
                $('#tbody tr[data-oid="' + single.id + '"]').first().html(datiColonna);
            });
            if (promises.length > 0) {
                for (var i = 0; i < promises.length; i++) {
                    let dataToFill = promises[i];
                    singlePromise(dataToFill);
                }
            }
        }
        if (result.totalPages == 0) {
            $("#tbody").html('<tr><td colspan="' + colonne.length + '">Nessun risultato corrispondente alla ricerca</td></tr>');
        } else {
            $('#paginazione').html(Paginazione.renderizzaComponente(result, {
                paginationWrapper: 'justify-content-center'
            }));
        }
    }

    function renderPagina(result_api) {
        $('#page_data').html(Pagina.renderPaginaBase(result_api));
    }


    $('#bottoneReset').click(function () {
        const vistaFiltro = new DtoFiltro();
        let query = window.location.search;
        for (var key of Object.keys(vistaFiltro)) {
            query = query.replace(new RegExp('&' + key + '=[^&]*', 'g'), '');
        }
        window.location.search = query;
    });

    function submit(idLegislatura, idAnno) {
        const vistaFiltro = new DtoFiltro();
        vistaFiltro.legislatura = idLegislatura;
        vistaFiltro.anno = idAnno;
        let query = window.location.search;
        for (var key of Object.keys(vistaFiltro)) {
            const val = vistaFiltro[key];
            query = query.replace(new RegExp('&?' + key + '=[^&]*', 'g'), '');
            if (val != null && val != "") {
                query += '&' + key + '=' + val;
            }
        }
        window.location.search = query;
    }

    function estrapolaFiltro() {
        const filtro = {};
        let legislatura = urlParams.get('legislatura');
        if (legislatura != null) {
            filtro['legislatura'] = legislatura;
            $('#legislatura').val(legislatura).change();
        } else {
            filtro['legislatura'] = legislature[0].id;
        }
        let anno = urlParams.get('anno');
        if (anno != null) {
            filtro['anno'] = anno;
            $('#anno').val(anno).change();
        } else {
            filtro['anno'] = legislature[0].anni[0];
        }
        return filtro;
    }

    $('.it-date-datepicker').datepicker({
        inputFormat: ["dd/MM/yyyy"],
        outputFormat: 'dd/MM/yyyy',
    });

    var urlParams = new URLSearchParams(window.location.search);
    let colonne = modelloTabella;

    var url_to_call = apibase.baseurl + "10202?size=10";
    url_to_call += Paginazione.inizializzaUrl(urlParams);

    var url_to_call_for_pagina = apibase.baseurl + "pagina/" + urlParams.get('section_id');


    $.ajax({
        method: 'GET',
        url: url_to_call_for_pagina,
        success: renderPagina,
        contentType: 'application/json',
        dataType: 'json'
    });

    $.ajax({
        method: 'POST',
        url: apibase.baseurl + 'dda/10300',
        success: function (risposta) {
            const data = new Date(risposta);
            const dataFormattata = StringRenderer.renderData(data, "/");
            $('#dataUltimoAggiornamento').html(dataFormattata);
        }
    });

    let legislature = [];

    function creaAnniLegislatura(resultLegislatura) {
        const anni = [];
        const dataInizio = new Date(resultLegislatura.properties.data_inizio);
        let dataFine = new Date();
        if (resultLegislatura.properties.data_fine) {
            dataFine = new Date(resultLegislatura.properties.data_fine);
        }
        const annoFine = dataFine.getFullYear();
        const annoInizio = dataInizio.getFullYear();
        if (annoFine === annoInizio) {
            return [annoInizio];
        }
        for (var i = annoFine; i >= annoInizio; i--) {
            anni.push(i);
        }
        return anni;
    }

    function trovaLegislatura(idLegislatura) {
        for (const legislatura of legislature) {
            if (legislatura.id == idLegislatura) {
                return legislatura;
            }
        }
        return null;
    }

    function initLegislature(idLegislatura, idAnno) {
        const selectLegislatura = $("#legislatura");
        legislature.forEach(function (legislatura) {
            selectLegislatura.append('<option value="' + legislatura.id + '">' + legislatura.titolo + '</option>');
        });
        const legislaturaSelezionata = trovaLegislatura(idLegislatura);
        const selectAnno = $('#anno');
        legislaturaSelezionata.anni.forEach(function (anno) {
            selectAnno.append('<option value="' + anno + '">' + anno + '</option>');
        });
        selectLegislatura.val(idLegislatura + '');
        selectAnno.val(idAnno + '');
        selectLegislatura.selectpicker('refresh');
        selectAnno.selectpicker('refresh');
        selectLegislatura.change(function () {
            const nuovaLegislatura = trovaLegislatura(selectLegislatura.val());
            submit(nuovaLegislatura.id, nuovaLegislatura.anni[0]);
        });
        selectAnno.change(function () {
            submit(idLegislatura, selectAnno.val());
        });
    }

    $.ajax({
        url: apibase.baseurl + 'legislature',
        success: function (result) {
            if (result && result.content) {
                legislature = result.content.map(function (resultLegislatura) {
                    const legislatura = new Legislatura();
                    legislatura.id = resultLegislatura.id;
                    legislatura.titolo = resultLegislatura.title;
                    legislatura.anni = creaAnniLegislatura(resultLegislatura);
                    return legislatura;
                });
            }
            const filtro = estrapolaFiltro();
            initLegislature(filtro.legislatura, filtro.anno);
            $.ajax({
                method: 'POST',
                url: url_to_call,
                data: JSON.stringify(filtro || {}),
                success: renderCompleto,
                contentType: 'application/json',
                dataType: 'json'
            });
        }
    })




});